*{
    margin: 0;
    padding: 0;
    /* box-sizing: border-box; */
    font-family: "Roboto", sans-serif;
    font-size: 22px;
}

body{
    background-image: url(../images/pic.jfif);
    background-size: cover;
    height: 850px;
    color: #FFF;
    margin: 0;
    padding: 0;
}

.site-info{
    position: absolute;
    left: 50%;
    top: 60%;
    transform: translate(-50%, -50%);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title{
    border: 2px solid #20B0D4;
    padding: 10px 20px;
    width: 300px;
    font-size: 28px;
    color: #2BB0E0;
    border-radius: 5px;
    letter-spacing: 8px;
    text-transform: uppercase;
}

.sub-title{
    font-size: 22px;
    letter-spacing: 6px;
    padding: 10px 0;
}

footer{
    position: absolute;
    bottom: 0;
    height: 60px;
    line-height: 60px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.social{
    padding-left: 20px;
}

.social i{
    font-size: 15px;
    padding-right: 15px;
}

